module.exports = function(elem, moveCB) {
  this.element = elem
  this.startCB = null
  this.moveCB = moveCB
  this.prevent = true		//Fixme?
  this.curID = null		//Unique id for each distinct touch
  this.startX = null		//Position on page where we started the touch
  this.startY = null
  this.offX = null		//Current (last) delta from start
  this.offY = null
  
  elem.addEventListener('touchstart',  (ev)=>{
    let touches = ev.changedTouches
    for (var i = 0; i < touches.length; i++) {		//For each changed touch event
      let touch = touches[i]
console.log("Touch start:", touch.identifier, this.curID)
      if (this.curID != null) {				//Already another touch in process on this element
//console.log("Take over:", touch.pageX, this.startX)
        this.startX = touch.pageX - this.offX
        this.startY = touch.pageY - this.offY
//console.log("To:", this.startX)
      } else {
        this.startX = touch.pageX
        this.startY = touch.pageY
//console.log("At:", this.startX)
      }
      this.curID = touch.identifier
    }
  },  false)
  
  elem.addEventListener('touchmove',   (ev)=>{
    let touches = ev.changedTouches
    for (var i = 0; i < touches.length; i++) {		//For each changed touch event
      let touch = touches[i]
      if (touch.identifier == this.curID) {
        this.moveCB(this.offX = touch.pageX - this.startX, this.offY = touch.pageY - this.startY)
      }
    }
  },   false)
  
  elem.addEventListener('touchend',    (ev)=>{
    let touches = ev.changedTouches
    for (var i = 0; i < touches.length; i++) {		//For each changed touch event
      let touch = touches[i]
      if (this.curID == touch.identifier) this.curID = null
console.log("Touch end:", touch.identifier, this.curID)
    }
  },    false)
  
  elem.addEventListener('touchcancel', (ev)=>{		//Not sure if/when this ever gets called
    let touches = ev.changedTouches
    for (var i = 0; i < touches.length; i++) {		//For each changed touch event
      let touch = touches[i]
      if (this.curID == touch.identifier) this.curID = null
console.log("Touch cancel:", touch.identifier, this.curID)
    }
  }, false)

}	//Class
